* {
  box-sizing: border-box;
  margin: 0 auto;
}

.app-container {
  height: 100vh;
  width: 470px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.board-container {
  height: 470px;
  width: 470px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.winning-board {
  animation: sizepulsate 3s ease-in-out infinite;
}

.info-container {
  height: 40px;
  text-align: center;
  line-height: 30px;
  margin-top: -100px;
  color: #fefae0;
}

.field {
  width: 150px;
  height: 150px;
  text-align: center;
  background-color: #e9edc9;
  border: 0.2px solid #ccd5ae;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.field:hover {
  border: 1px solid #343a40;
}

.symbol {
  font-size: 120px;
  font-weight: 200;
  color: #283618;
  position: relative;
  top: -15px;
}

.restart-button {
  animation: pulsate 1s ease-out infinite;
  opacity: 0.5;
  color: #fefae0;
  cursor: pointer;
}

@keyframes pulsate {
  0% { 
    opacity: 0.5;
  }
  50% { 
    opacity: 1;
  }
  100% { 
    opacity: 0.5;
  }
}

@keyframes sizepulsate {
  0% { 
    scale: 1;
  }
  50% { 
    scale: 0.95;
  }
  100% { 
    scale: 1;
  }
}